<template>
  <input
    type="checkbox"
    class="Checkbox"
  />
</template>

<script>
export default {
  props: ['mounted'],
  mounted() {
    if (this.mounted instanceof Function) {
      this.mounted(this.$el.name, this.$el.checked, this.$el);
    }
  },
};
</script>

<style scoped>
.Checkbox {
  border: 1px solid #979797;
  width: 20px;
  height: 20px;
  -webkit-appearance: none;
  -moz-appearance: none;
  outline: none;
  vertical-align: bottom;
  cursor: pointer;
  background-color: #FFF;
}

.Checkbox:checked::after {
  content: '\2713';
  color: #5ab400;
  width: 100%;
  height: 100%;
  font-size: 16px;
  font-weight: bold;
  text-align: 'center';
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>



